import React from "react";
import styles from "./styles";
import { withStyles } from "@material-ui/core/styles";
import "./ChatView.scss";
import images from "../Themes/Images";

// import TextField from "@material-ui/core/TextField";
import "./chatView.css";
import ReactLoading from "react-loading";
// import Tooltip from "@material-ui/core/Tooltip";
import moment from "moment";
import Avatar from "@material-ui/core/Avatar";
// import Send from "@material-ui/icons/Send";
// import TextField from "@material-ui/core/TextField";
class ChatViewComponent extends React.Component {
  constructor() {
    super();
    this.state = {
      chatText: "",
    };
  }
  componentDidMount = () => {
    const container = document.getElementById("chatview-container");
    if (container) container.scrollTo(0, container.scrollHeight);
  };
  componentDidUpdate = () => {
    this.scrollToBottom();
  };
  scrollToBottom = () => {
    if (this.messagesEnd) {
      this.messagesEnd.scrollIntoView({});
    }
  };
  messageValid = (txt) => txt && txt.replace(/\s/g, "").length;
  userClickedInput = () => this.props.userClickedInputFn();
  submitMessage = () => {
    if (this.messageValid(this.state.chatText)) {
      this.props.submitMessageFn(this.state.chatText);
      document.getElementById("chattextbox").value = "";
    }
  };
  render() {
    const { classes } = this.props;

    if (this.props.chat === undefined) {
      return <main className={classes.content}></main>;
    } else if (this.props.chat !== undefined) {
      return (
        <div className="viewChatBoard">
          <div className="headerChatBoard">
            <Avatar alt="Remy Sharp">
              {
                this.props.chat.users
                  .filter((_user) => _user !== this.props.userEmail)[0]
                  .split("")[0]
              }
            </Avatar>
            <span className="textHeaderChatBoard">
              {
                this.props.chat.users.filter(
                  (_usr) => _usr !== this.props.user
                )[0]
              }
            </span>
          </div>
          {/* <div id="chat-title">
            Your conversation with{" "}
            {
              this.props.chat.users.filter(
                (_usr) => _usr !== this.props.user
              )[0]
            }
          </div> */}

          {/* <main className={classes.content}> */}
          <div className="viewListContentChat">
            {this.props.chat.messages.map((_msg, _index) => {
              return (
                <div
                  key={_index}
                  className={
                    _msg.sender === this.props.user
                      ? classes.userSent
                      : classes.friendSent
                  }
                >
                  <div className="viewListContentChat">
                    {/* <Tooltip
                      title={new Date(parseInt(_msg.timestamp)).toUTCString()}
                    > */}
                    <div>{_msg.message}</div>
                    <span className="textTimeLeft">
                      {moment(Number(_msg.timestamp)).format("lll")}
                    </span>

                    {/* </Tooltip> */}
                  </div>

                  {/* <div className={classes.Conversationtime}>
                    {" "}
                    <span>
                      {_msg.receiverHasRead === true ? "yrdy" : "seen"}
                    </span>
                  </div> */}
                </div>
              );
            })}
            <div
              style={{ float: "left", clear: "both" }}
              ref={(el) => {
                this.messagesEnd = el;
              }}
            />
          </div>

          {/* </main> */}
        </div>
      );
    } else {
      return (
        <div className="viewLoading">
          <ReactLoading
            type={"bars"}
            color={"#203152"}
            height={"3%"}
            width={"3%"}
          />
        </div>
      );
    }
  }
}

export default withStyles(styles)(ChatViewComponent);
