import React from "react";
import NewChatComponent from "../NewChat/newChat";
import ChatListComponent from "../chatlist/chatList";
import ChatViewComponent from "../ChatView/chatView.js";
import ChatTextBoxComponent from "../ChatTextBox/chatTextBox";
import Typography from "@material-ui/core/Typography";
import styles from "./styles";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core";
import Tooltip from "@material-ui/core/Tooltip";
// import CircularProgress from "@material-ui/core/CircularProgress";
import ReactLoading from "react-loading";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import "./dashboard.css";
// import Paper from "@material-ui/core/Paper";
// import MenuIcon from "@material-ui/icons/Menu";
// import Hidden from '@material-ui/core/Hidden';
// import Menu from "@material-ui/core/Menu";
// import MenuItem from "@material-ui/core/MenuItem";

// import AccountCircleIcon from "@material-ui/icons/AccountCircle";
const firebase = require("firebase");

// I need to investigate why sometimes
// two messages will send instead of just
// one. I dont know if there are two instances
// of the chat box component or what...

// I will be using both .then and async/await
// in this tutorial to give a feel of both.

class DashboardComponent extends React.Component {
  constructor() {
    super();
    this.state = {
      selectedChat: null,
      newChatFormVisible: false,
      email: null,
      friends: [],
      chats: [],
      showHideChatList: true,
    };
    this.hideComponent = this.hideComponent.bind(this);
  }
  hideComponent(name) {
    switch (name) {
      case "showHideChatList":
        this.setState({
          showHideChatList: !this.state.showHideChatList,
        });

        break;
      default:
    }
  }

  render() {
    const { classes } = this.props;
    const { showHideChatList } = this.state;

    if (this.state.email) {
      return (
        <div className="viewChatBoard">
          <AppBar position="fixed" color="primary" className={classes.appBar}>
            <Toolbar>
              <Tooltip title="Show Friend List">
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                >
                  <MenuIcon
                    onClick={() => this.hideComponent("showHideChatList")}
                  />
                </IconButton>
              </Tooltip>
              <Typography variant="h6" className={classes.title}>
                <Tooltip title="Add Friend">
                  <Fab
                    size="medium"
                    color="secondary"
                    aria-label="add"
                    onClick={this.newChatBtnClicked}
                  >
                    <AddIcon />
                  </Fab>
                </Tooltip>
              </Typography>

              <Button color="inherit" onClick={this.signOut}>
                LogOut
              </Button>
              {/* <IconButton
                edge="start"
                className={classes.menuButton}
                color="secondary"
                aria-label="menu"
              >
                <AccountCircleIcon />
              </IconButton> */}
            </Toolbar>
          </AppBar>
          <div className="viewListUser">
            {showHideChatList && (
              <ChatListComponent
                history={this.props.history}
                userEmail={this.state.email}
                selectChatFn={this.selectChat}
                chats={this.state.chats}
                selectedChatIndex={this.state.selectedChat}
                newChatBtnFn={this.newChatBtnClicked}
              ></ChatListComponent>
            )}
          </div>
          <div className="viewBoard">
            {this.state.showHideChatList ||
            this.state.newChatFormVisible ? null : (
              <ChatViewComponent
                user={this.state.email}
                chat={this.state.chats[this.state.selectedChat]}
                userClickedInputFn={this.messageRead}
                submitMessageFn={this.submitMessage}
              ></ChatViewComponent>
            )}

            {this.state.selectedChat !== null &&
            !this.state.newChatFormVisible ? (
              <ChatTextBoxComponent
                userClickedInputFn={this.messageRead}
                submitMessageFn={this.submitMessage}
              ></ChatTextBoxComponent>
            ) : null}

            {this.state.newChatFormVisible && !this.state.showHideChatList ? (
              <NewChatComponent
                goToChatFn={this.goToChat}
                newChatSubmitFn={this.newChatSubmit}
              ></NewChatComponent>
            ) : null}
          </div>

          {/* <Button onClick={this.signOut} className={classes.signOutBtn}>LogOut</Button> */}
        </div>
      );
    } else {
      return (
        <div className="viewLoading">
          <ReactLoading
            type={"bars"}
            color={"#0390fc"}
            height={"15%"}
            width={"15%"}
          />
        </div>
      );
    }
  }

  signOut = () => firebase.auth().signOut();
  userIsSender = (chat) =>
    chat.messages[chat.messages.length - 1].sender === this.props.userEmail;
  newChat = () => this.props.newChatBtnFn();
  selectChat = (index) => this.props.selectChatFn(index);
  submitMessage = (msg) => {
    const docKey = this.buildDocKey(
      this.state.chats[this.state.selectedChat].users.filter(
        (_usr) => _usr !== this.state.email
      )[0]
    );
    firebase
      .firestore()
      .collection("chats")
      .doc(docKey)
      .update({
        messages: firebase.firestore.FieldValue.arrayUnion({
          sender: this.state.email,
          message: msg,
          timestamp: Date.now(),
        }),
        receiverHasRead: false,
      });
  };

  // Always in alphabetical order:
  // 'user1:user2'
  buildDocKey = (friend) => [this.state.email, friend].sort().join(":");

  newChatBtnClicked = () =>
    this.setState({
      newChatFormVisible: true,
      selectedChat: null,
      showHideChatList: false,
    });

  newChatSubmit = async (chatObj) => {
    const docKey = this.buildDocKey(chatObj.sendTo);
    await firebase
      .firestore()
      .collection("chats")
      .doc(docKey)
      .set({
        messages: [
          {
            message: chatObj.message,
            sender: this.state.email,
          },
        ],
        users: [this.state.email, chatObj.sendTo],
        receiverHasRead: false,
      });
    this.setState({ newChatFormVisible: false });
    this.selectChat(this.state.chats.length - 1);
  };

  selectChat = async (chatIndex) => {
    await this.setState({
      selectedChat: chatIndex,
      newChatFormVisible: false,
      showHideChatList: false,
    });
    this.messageRead();
  };

  goToChat = async (docKey, msg) => {
    const usersInChat = docKey.split(":");
    const chat = this.state.chats.find((_chat) =>
      usersInChat.every((_user) => _chat.users.includes(_user))
    );
    this.setState({ newChatFormVisible: false });
    await this.selectChat(this.state.chats.indexOf(chat));
    this.submitMessage(msg);
  };

  // Chat index could be different than the one we are currently on in the case
  // that we are calling this function from within a loop such as the chatList.
  // So we will set a default value and can overwrite it when necessary.
  messageRead = () => {
    const chatIndex = this.state.selectedChat;
    const docKey = this.buildDocKey(
      this.state.chats[chatIndex].users.filter(
        (_usr) => _usr !== this.state.email
      )[0]
    );
    if (this.clickedMessageWhereNotSender(chatIndex)) {
      firebase
        .firestore()
        .collection("chats")
        .doc(docKey)
        .update({ receiverHasRead: true });
    } else {
      console.log("Clicked message where the user was the sender");
    }
  };

  clickedMessageWhereNotSender = (chatIndex) =>
    this.state.chats[chatIndex].messages[
      this.state.chats[chatIndex].messages.length - 1
    ].sender !== this.state.email;

  componentWillMount = () => {
    firebase.auth().onAuthStateChanged(async (_usr) => {
      if (!_usr) this.props.history.push("/login");
      else {
        await firebase
          .firestore()
          .collection("chats")
          .where("users", "array-contains", _usr.email)
          .onSnapshot(async (res) => {
            const chats = res.docs.map((_doc) => _doc.data());
            await this.setState({
              email: _usr.email,
              chats: chats,
              friends: [],
            });
          });
      }
    });
  };
}

export default withStyles(styles)(DashboardComponent);
